  height: 12px;
}
.hero-card__link-select--days::before {
  background-image: url("../img/svg/moon.svg");
}
.hero-card__link-select--level::before {
  background-image: url("../img/svg/level.svg");
}

.hero {
  padding: 0 calc(50% - 720px);
}
@media (max-width: 1199px) {
  .hero {
    padding: 0;
  }
}

.hero__wrapper {
  position: relative;
}

.hero__pagination {
  position: absolute;
  bottom: 100px;
  right: 120px;
  z-index: 5;
}

.page-header {
  position: relative;
}

.page-header__nav {
  position: absolute;
  display: flex;
  align-items: center;
  padding: 40px calc(50% - 600px);
  width: 100%;
  color: white;
  z-index: 10;
}
@media (max-width: 1199px) {
  .page-header__nav {
    justify-content: space-between;
    padding: 30px 45px;
    width: 100%;
  }
}
@media (max-width: 767px) {
  .page-header__nav {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding: 15px;
  }
}
.page-header__nav--opened {
  background-color: white;
  color: black;
}

.page-header__toggle {
  display: none;
  padding: 0;
  background-color: inherit;
  border: 0;
  cursor: pointer;
}
@media (max-width: 1199px) {
  .page-header__toggle {
    display: block;
    margin-bottom: 6px;
    width: 26px;
    height: 26px;
  }
}
@media (max-width: 767px) {
  .page-header__toggle {
    margin-top: -12px;
  }
}
.page-header__toggle--closed {
  background-image: url("../img/sprite/menu.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.page-header__toggle--opened {
  margin-left: -2px;
  background-image: url("../img/sprite/cross.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.page-header__logo-link {
  margin-right: 133px;
}
@media (max-width: 1199px) {
  .page-header__logo-link {
    margin-right: 0;
    margin-left: 100px;
  }
}
@media (max-width: 767px) {
  .page-header__logo-link {
    justify-self: end;
  }
}

.page-header__icon {
  color: inherit;
}
@media (max-width: 1199px) {
  .page-header__icon {
    width: 144px;
    height: 44px;
  }
}
@media (max-width: 767px) {
  .page-header__icon {
    width: 112px;
    height: 34px;
  }
}

.page-header__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 3px -22px;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 1199px) {
  .page-header__list {
    display: none;
    margin: 0;
    padding-bottom: 57px;
  }
}
@media (max-width: 767px) {
  .page-header__list {
    padding-bottom: 41px;
  }
}
.page-header__list--opened {
  position: absolute;
  top: 111px;
  left: 0;
  right: 0;
  display: grid;
  grid-template-columns: 1fr;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .page-header__list--opened {
    top: 86px;
  }
}

.page-header__link {
  position: relative;
  margin-left: 22px;
  padding: 3px 5px;
  line-height: 16px;
}
@media (max-width: 1199px) {
  .page-header__link {
    margin-left: 0;
    padding: 13px 45px;
    font-weight: 700;
    letter-spacing: 0.64px;
    text-transform: uppercase;
  }
}
@media (max-width: 767px) {
  .page-header__link {
    padding: 11px 15px;
    font-size: 14px;
    line-height: 14px;
    letter-spacing: 0.56px;
  }
}
@media (hover: hover) {
  .page-header__link:hover:not(.focus-visible) {
    left: 5px;
  }
  .page-header__link:hover:not(.focus-visible)::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 1px;
    background-color: #ffffff;
  }
}
.page-header__link.focus-visible:focus {
  padding: 2px 4px;
  border: 1px solid #ffffff;
  outline: none;
}
.page-header__link[aria-disabled=true] {
  color: rgba(255, 255, 255, 0.5);
  pointer-events: none;
}

.page-header__wrapper {
  margin-left: auto;
  margin-bottom: 3px;
}
@media (max-width: 1199px) {
  .page-header__wrapper {
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .page-header__wrapper {
    grid-column: 1/-1;
    justify-self: end;
    margin-top: 1px;
    margin-bottom: 0;
  }
}

.page-header__phone-link {
  font-weight: 600;
  font-size: 18px;
  line-height: 18px;
}
@media (max-width: 1199px) {
  .page-header__phone-link {
    font-size: 16px;
    line-height: 16px;
  }
}
@media (max-width: 767px) {
  .page-header__phone-link {
    font-size: 14px;
    line-height: 14px;
  }
}

.radio {
  display: grid;
  grid-template-columns: 1fr;
  padding: 40px;
  color: #1c3374;
}

.radio__title {
  margin: 0;
  font-weight: 400;
  font-size: 22px;
  line-height: 22px;
}

.radio__block-interaction {
  display: grid;
  grid-template-columns: min-content 1fr;
}

.radio__subtitle {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  color: rgba(28, 51, 116, 0.48);
}

.radio__list {
  display: flex;
  flex-wrap: wrap;
  margin: -4px 0 0 auto;
  padding: 0;
  list-style-type: none;
}

.radio__link-action {
  margin-left: 14px;
}

.radio__block-range {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.radio__range {
  position: relative;
  grid-column: 1/-1;
  height: 2px;
  background-color: rgba(28, 51, 116, 0.4);
}

.radio__range-bar {
  position: absolute;
  left: 0;
  width: 148px;
  height: 2px;
  background-color: #1c3374;
  z-index: 5;
}

.radio__range-button {
  position: absolute;
  top: -3px;
  right: -3px;
  width: 8px;
  height: 8px;
  background-color: #1c3374;
  border: 0;
  border-radius: 50%;
}

.radio__time {
  display: block;
  font-size: 10px;
  line-height: 10px;
}
.radio__time--full {
  margin-left: auto;
}

.swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  background: inherit;
  border: 1px solid #ffffff;
  opacity: 1;
}

.swiper-pagination-bullet-active {
  background-color: #ffffff;
}

.video__player {
  display: none;
}

.video.is-active .video__player {
  display: block;
}
.video.is-active .video__control {
  display: none;
}

.video__control {
  position: absolute;
  top: 131px;
  left: 213px;
  padding: 0;
  width: 56px;
  height: 56px;
  background-color: inherit;
  background-image: url("../img/svg/play-default.svg");
  border: 0;
  cursor: pointer;
}
@media (max-width: 1199px) {
  .video__control {
    top: 122px;
    left: 193px;
  }
}
@media (hover: hover) {
  .video__control:hover:not(.focus-visible) {
    background-image: url("../img/svg/play-hover.svg");
  }
}
.video__control.focus-visible:focus {
  background-image: url("../img/svg/play-hover.svg");
  outline: none;
}
.video__control:active {
  background-image: url("../img/svg/play-active.svg");
}
.video__control:disabled {
  background-image: url("../img/svg/play-disabled.svg");
  pointer-events: none;
}

